import React from "react";
import Grid from "@mui/material/Grid";
import AppBar from "@mui/material/AppBar";
import Box from "@mui/material/Box";
import Toolbar from "@mui/material/Toolbar";
import IconButton from "@mui/material/IconButton";
import Typography from "@mui/material/Typography";
import Menu from "@mui/material/Menu";
import MenuIcon from "@mui/icons-material/Menu";
import Container from "@mui/material/Container";
import Avatar from "@mui/material/Avatar";
import Button from "@mui/material/Button";
import Tooltip from "@mui/material/Tooltip";
import MenuItem from "@mui/material/MenuItem";
import AdbIcon from "@mui/icons-material/Adb";
import LanguageSwitch from "./LanguageSwitch";
import { makeStyles } from "@mui/styles";
import { Trans, useTranslation } from "react-i18next";
import { Translation } from "react-i18next";
import { Link } from "react-router-dom";
import { publicRoutes } from "../../../../routes";

const pages = ["Home", "Brand", "Product", "Service", "Contact"];
const useStyles = makeStyles((theme) => ({
  header: {
    backgroundColor: "var(--primary-color) !important",
    maxHeight: "60px",
    position:"fixed !important",
    top:"0",
    left:"0"
  },
  menu: {
    backgroundColor: "var(--primary-color) !important",
  },
}));

function Header(props) {
  const classes = useStyles();
  const [anchorElNav, setAnchorElNav] = React.useState(null);
  const { t } = useTranslation();
  const handleOpenNavMenu = (event) => {
    setAnchorElNav(event.currentTarget);
  };

  const handleCloseNavMenu = () => {
    setAnchorElNav(null);
  };
  return (
    <AppBar position="static" classes={{ root: classes.header }}>
      <Container maxWidth="xl">
        <Toolbar disableGutters>
          <Box sx={{ width: "100px" }}>
            <Typography
              variant="h6"
              noWrap
              component="a"
              href="/"
              sx={{
                mr: 2,
                display: { xs: "none", md: "flex" },
                fontFamily: "monospace",
                fontWeight: 700,
                letterSpacing: ".3rem",
                color: "inherit",
                textDecoration: "none",
              }}
            >
              LOGO
            </Typography>
          </Box>

          <Box sx={{ flexGrow: 1, display: { xs: "flex", md: "none" } }}>
            <IconButton
              size="large"
              aria-label="account of current user"
              aria-controls="menu-appbar"
              aria-haspopup="true"
              onClick={handleOpenNavMenu}
              color="inherit"
            >
              <MenuIcon />
            </IconButton>
            <Menu
              id="menu-appbar"
              anchorEl={anchorElNav}
              anchorOrigin={{
                vertical: "bottom",
                horizontal: "left",
              }}
              keepMounted
              transformOrigin={{
                vertical: "top",
                horizontal: "left",
              }}
              open={Boolean(anchorElNav)}
              onClose={handleCloseNavMenu}
              sx={{
                display: { xs: "block", md: "none" },
              }}
            >
              {pages.map((page) => (
                <MenuItem key={page} onClick={handleCloseNavMenu}>
                  <Typography textAlign="center">{page}</Typography>
                </MenuItem>
              ))}
            </Menu>
          </Box>
          <Typography
            variant="h5"
            noWrap
            component="a"
            href=""
            sx={{
              mr: 2,
              display: { xs: "flex", md: "none" },
              flexGrow: 1,
              fontFamily: "monospace",
              fontWeight: 700,
              letterSpacing: ".3rem",
              color: "inherit",
              textDecoration: "none",
            }}
          >
            LOGO
          </Typography>
          <Box
            sx={{
              flexGrow: 1,
              display: { xs: "none", md: "flex" },
              justifyContent: "center",
            }}
          >
            {pages.map((page) => (

              <Button
                key={page}
                onClick={handleCloseNavMenu}
                sx={{ 
                  m: 1, 
                  color: "white", 
                  display: "block", 
                  fontSize:"1rem", 
                  fontWeight:"bold",
                  borderRadius:"unset",
                  transition: "0.25s",
                  '&:hover': {
                    // boxShadow:  "0px 2px 0px #ffffff"
                    boxShadow: "inset 0 -2px 0 0 #ffffff"
                  },
                }}
              >
                {t(page)}
              </Button>
              
            ))}
          </Box>

          <Box sx={{ flexGrow: 0, width: "100px",justifyContent: "flex-end", display: 'flex'}}>
            <LanguageSwitch />
          </Box>
        </Toolbar>
      </Container>
    </AppBar>
  );
}

export default Header;
